* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', 'Roboto', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #213448 0%, #1a2a3a 25%, #0f1419 50%, #0a0f14 75%, #000000 100%);
  color: #ECEFCA;
  overflow-x: hidden;
}

.app {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #213448 0%, #1a2a3a 25%, #0f1419 50%, #0a0f14 75%, #000000 100%);
  position: relative;
  overflow: hidden;
}

.app::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(ellipse at 20% 30%, rgba(84, 119, 146, 0.05) 0%, transparent 50%),
    radial-gradient(ellipse at 80% 70%, rgba(148, 180, 193, 0.03) 0%, transparent 50%),
    radial-gradient(ellipse at 40% 80%, rgba(33, 52, 72, 0.08) 0%, transparent 40%);
  pointer-events: none;
}

.app::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1200px;
  height: 1200px;
  transform: translate(-50%, -50%);
  background: radial-gradient(ellipse at center, 
    transparent 0%, 
    transparent 20%, 
    rgba(33, 52, 72, 0.1) 30%,
    rgba(33, 52, 72, 0.3) 50%,
    rgba(15, 20, 25, 0.6) 70%,
    rgba(0, 0, 0, 0.8) 90%,
    rgba(0, 0, 0, 0.95) 100%);
  border-radius: 50%;
  animation: devour 15s ease-in-out infinite, slowly-rotate 60s linear infinite;
  pointer-events: none;
  z-index: 2;
}

@keyframes devour {
  0%, 100% { 
    opacity: 0.4; 
    transform: translate(-50%, -50%) scale(1); 
  }
  50% { 
    opacity: 0.8; 
    transform: translate(-50%, -50%) scale(1.1); 
  }
}

@keyframes slowly-rotate {
  0% { transform: translate(-50%, -50%) rotate(0deg); }
  100% { transform: translate(-50%, -50%) rotate(360deg); }
}

.home-container {
  text-align: center;
  width: 100%;
  max-width: 600px;
  padding: 20px;
  position: relative;
  z-index: 10;
  transform-style: preserve-3d;
}

.logo-section {
  margin-bottom: 50px;
  position: relative;
  transform-style: preserve-3d;
}

.logo {
  font-size: 5.5rem;
  font-weight: 200;
  color: #ECEFCA;
  margin-bottom: 20px;
  letter-spacing: -3px;
  position: relative;
  text-shadow: 
    0 0 30px rgba(236, 239, 202, 0.3),
    0 5px 10px rgba(33, 52, 72, 0.5),
    0 10px 20px rgba(33, 52, 72, 0.3),
    0 15px 30px rgba(33, 52, 72, 0.2);
  transform: perspective(1000px) rotateX(10deg);
  animation: logo-float 6s ease-in-out infinite;
}

@keyframes logo-float {
  0%, 100% {
    transform: perspective(1000px) rotateX(10deg) translateZ(0px);
  }
  50% {
    transform: perspective(1000px) rotateX(15deg) translateZ(10px);
  }
}

.logo::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(90deg, transparent, #94B4C1, transparent);
  border-radius: 2px;
  box-shadow: 
    0 0 10px rgba(148, 180, 193, 0.5),
    0 0 20px rgba(148, 180, 193, 0.3);
}

.tagline {
  color: #94B4C1;
  font-size: 1.1rem;
  font-weight: 300;
  margin-top: 15px;
}

.search-form {
  margin-bottom: 40px;
}

.search-box {
  position: relative;
  display: flex;
  align-items: center;
  background: #ECEFCA;
  border-radius: 32px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.4),
    0 20px 60px rgba(33, 52, 72, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1),
    inset 0 -1px 0 rgba(84, 119, 146, 0.1);
  overflow: hidden;
  max-width: 584px;
  margin: 0 auto;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid transparent;
  transform: perspective(1000px) rotateX(5deg) translateZ(20px);
}

.search-box:hover {
  transform: perspective(1000px) rotateX(8deg) translateZ(40px) translateY(-5px);
  box-shadow: 
    0 15px 50px rgba(0, 0, 0, 0.5),
    0 30px 80px rgba(33, 52, 72, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2),
    inset 0 -1px 0 rgba(84, 119, 146, 0.2);
}

.search-box:focus-within {
  border-color: #547792;
  transform: perspective(1000px) rotateX(10deg) translateZ(50px) translateY(-8px);
  box-shadow: 
    0 20px 60px rgba(0, 0, 0, 0.6),
    0 40px 100px rgba(33, 52, 72, 0.5),
    0 0 0 3px rgba(84, 119, 146, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.search-icon {
  position: absolute;
  left: 20px;
  color: #547792;
  z-index: 1;
}

.search-input {
  width: 100%;
  padding: 18px 60px 18px 56px;
  border: none;
  outline: none;
  font-size: 17px;
  background: transparent;
  color: #213448;
  font-weight: 400;
}

.search-input::placeholder {
  color: #547792;
  opacity: 0.8;
}

.search-button {
  position: absolute;
  right: 10px;
  background: #547792;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ECEFCA;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 2px 8px rgba(84, 119, 146, 0.3);
}

.search-button:hover:not(:disabled) {
  background: #213448;
  transform: scale(1.05);
  box-shadow: 0 4px 12px rgba(33, 52, 72, 0.4);
}

.search-results-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #213448 0%, #1a2a3a 25%, #0f1419 50%, #0a0f14 75%, #000000 100%);
  color: #ECEFCA;
  position: relative;
  overflow-x: hidden;
}

.search-header {
  border-bottom: 2px solid rgba(148, 180, 193, 0.3);
  padding: 16px 0;
  background: rgba(33, 52, 72, 0.9);
  backdrop-filter: blur(10px);
  position: sticky;
  top: 0;
  z-index: 20;
  z-index: 100;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.search-header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  align-items: center;
  gap: 40px;
}

.logo-small {
  font-size: 28px;
  font-weight: 300;
  color: #213448;
  cursor: pointer;
  min-width: 140px;
  position: relative;
  transition: color 0.2s ease;
}

.logo-small:hover {
  color: #547792;
}

.logo-small::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 2px;
  background: #94B4C1;
  transition: width 0.3s ease;
}

.logo-small:hover::after {
  width: 100%;
}

.search-form-header {
  flex: 1;
  max-width: 500px;
}

.search-box-header {
  position: relative;
  display: flex;
  align-items: center;
  background: white;
  border: 2px solid #94B4C1;
  border-radius: 28px;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.search-box-header:focus-within {
  border-color: #547792;
  box-shadow: 
    0 4px 16px rgba(84, 119, 146, 0.2),
    0 0 0 3px rgba(84, 119, 146, 0.1);
  transform: translateY(-1px);
}

.search-icon-header {
  position: absolute;
  left: 16px;
  color: #547792;
  z-index: 1;
}

.search-input-header {
  width: 100%;
  padding: 14px 44px 14px 44px;
  border: none;
  outline: none;
  font-size: 16px;
  background: transparent;
  color: #213448;
}

.search-button-header {
  position: absolute;
  right: 6px;
  background: #547792;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ECEFCA;
  cursor: pointer;
  transition: all 0.2s ease;
}

.search-button-header:hover {
  background: #213448;
  transform: scale(1.05);
}

.header-actions {
  display: flex;
  gap: 8px;
}

.header-icon {
  color: #547792;
  cursor: pointer;
  padding: 10px;
  border-radius: 50%;
  transition: all 0.2s ease;
  border: 1px solid transparent;
}

.header-icon:hover {
  background: #94B4C1;
  color: #213448;
  border-color: #547792;
}

.search-results-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 0 24px;
  position: relative;
  z-index: 10;
}

.search-tabs {
  display: flex;
  gap: 32px;
  padding: 16px 0;
  border-bottom: 1px solid #94B4C1;
  margin-bottom: 24px;
}

.tab {
  padding: 12px 0;
  color: #547792;
  cursor: pointer;
  font-size: 15px;
  font-weight: 500;
  border-bottom: 3px solid transparent;
  transition: all 0.2s ease;
  position: relative;
}

.tab:hover {
  color: #213448;
}

.tab.active {
  color: #213448;
  border-bottom-color: #547792;
}

.tab.active::before {
  content: '';
  position: absolute;
  top: -1px;
  left: -8px;
  right: -8px;
  bottom: -1px;
  background: rgba(84, 119, 146, 0.05);
  border-radius: 8px;
  z-index: -1;
}

.search-stats {
  color: #547792;
  font-size: 14px;
  margin-bottom: 24px;
  font-weight: 400;
}

.results-list {
  margin-top: 24px;
}

.result-item {
  margin-bottom: 32px;
  padding: 20px 0;
  border-bottom: 1px solid rgba(148, 180, 193, 0.2);
  transition: all 0.2s ease;
}

.result-item:hover {
  padding-left: 12px;
  border-left: 4px solid #94B4C1;
}

.result-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 4px;
}

.globe-icon {
  color: #94B4C1;
}

.result-url {
  color: #547792;
  font-size: 14px;
  text-decoration: none;
  font-weight: 500;
}

.result-title {
  margin-bottom: 6px;
}

.result-title a {
  color: #213448;
  text-decoration: none;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.3;
  transition: color 0.2s ease;
}

.result-title a:hover {
  color: #547792;
  text-decoration: underline;
  text-decoration-color: #94B4C1;
  text-decoration-thickness: 2px;
  text-underline-offset: 3px;
}

.result-snippet {
  color: #547792;
  line-height: 1.6;
  font-size: 14px;
  margin-top: 4px;
}

.no-results {
  text-align: center;
  padding: 80px 20px;
  color: #547792;
  background: rgba(148, 180, 193, 0.05);
  border-radius: 16px;
  margin: 40px 0;
  border: 2px dashed #94B4C1;
}

.no-results-icon {
  color: #94B4C1;
  margin-bottom: 24px;
}

.no-results h3 {
  font-size: 1.6rem;
  margin-bottom: 12px;
  color: #213448;
  font-weight: 300;
}

.no-results p {
  font-size: 1rem;
  color: #547792;
  margin-bottom: 24px;
  line-height: 1.5;
}

.crawl-button {
  background: #547792;
  color: #ECEFCA;
  border: 2px solid #547792;
  padding: 14px 28px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 4px 12px rgba(84, 119, 146, 0.3);
}

.crawl-button:hover {
  background: #213448;
  border-color: #213448;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(33, 52, 72, 0.4);
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 3px solid rgba(236, 239, 202, 0.3);
  border-top: 3px solid #ECEFCA;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

.loading-spinner-small {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(236, 239, 202, 0.3);
  border-top: 2px solid #ECEFCA;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@media (max-width: 768px) {
  .logo {
    font-size: 4rem;
  }
  
  .search-header-content {
    padding: 0 16px;
    gap: 20px;
  }
  
  .search-results-container {
    padding: 0 16px;
  }
  
  .search-tabs {
    gap: 20px;
    overflow-x: auto;
    scrollbar-width: none;
  }
  
  .search-tabs::-webkit-scrollbar {
    display: none;
  }
}

@media (max-width: 480px) {
  .logo {
    font-size: 3.5rem;
  }
  
  .app::before {
    opacity: 0.05;
  }
  
  .search-box {
    margin: 0 16px;
  }
  
  .result-item:hover {
    padding-left: 8px;
  }
}
